import React, {useState} from 'react'

import {observer} from 'mobx-react-lite'
import './HeaderApp.scss'
import routes from "../../../app/lib/routes";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faBookmark, faHome} from "@fortawesome/free-solid-svg-icons";

import {useEffect} from 'react'
import {themeChange} from 'theme-change'

export const HeaderApp = observer(({children}: any) => {

    const [state, setState] = useState(false)
    const themes = ["light", "dark", "cupcake", "bumblebee", "emerald", "corporate", "synthwave", "retro", "cyberpunk", "valentine", "halloween", "garden", "forest", "aqua", "lofi", "pastel", "fantasy", "wireframe", "black", "luxury", "dracula", "cmyk", "autumn", "business", "acid", "lemonade", "night", "coffee", "winter"]
    useEffect(() => {
        themeChange(false)
    }, [])
    return (
        <div className="drawer drawer-mobile">
            <input id="my-drawer-2" type="checkbox" className="drawer-toggle" onClick={() => setState(!state)}
                   checked={state}/>
            <div className="drawer-content  bg-base-100">
                {children}
                <label htmlFor="my-drawer-2" className="btn btn-primary drawer-button lg:hidden menu-btn">
                    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24"
                         className="inline-block w-5 h-5 stroke-current">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2"
                              d="M4 6h16M4 12h16M4 18h16"></path>
                    </svg>
                </label>
            </div>
            <div className="drawer-side"><label
                htmlFor="drawer" className="drawer-overlay"></label>
                <aside className="bg-base-200 w-60">
                    <div
                        className="z-20 bg-base-200 bg-opacity-90 backdrop-blur sticky top-0 items-center gap-2 px-4 py-2 hidden lg:flex shadow-sm">
                        <a href="/" aria-current="page" aria-label="Homepage" className="flex-0 btn btn-ghost px-2">
                            <div
                                className="font-title text-primary inline-flex text-lg transition-all duration-200 md:text-3xl">
                                <span className="lowercase">Net</span> <span
                                className="text-base-content uppercase">Book</span>
                            </div>
                        </a> <a className="link link-hover font-mono text-xs text-opacity-50">

                    </a></div>
                    <div className="h-4"></div>

                    <ul className="menu p-4 w-60 text-base-content bg-opacity-90">
                        <li><a href={"#" + routes.HOME} onClick={() => setState(!state)}><FontAwesomeIcon
                            icon={faHome}/></a></li>
                        <li><a href={"#" + routes.FAVOURITES} onClick={() => setState(!state)}><FontAwesomeIcon
                            icon={faBookmark}/></a></li>
                    </ul>

                    <div className="bg-base-200 dropdown dropdown-top">
                        <label tabIndex={0} className="btn  m-1"></label>
                        <div className={'dropdown-kok'}>
                            <ul tabIndex={0} className="dropdown-content menu p-2 shadow bg-base-100 rounded-box w-52" >
                                {
                                    themes.map((data) => (
                                        <li><a data-set-theme={data} data-act-class="ACTIVECLASS">{data}</a></li>
                                    ))
                                }
                            </ul>
                        </div>
                    </div>
                </aside>
            </div>
        </div>
    )
})
